

*{
    margin: 0;
    padding: 0;
}


.error{
    color: red;
    text-transform: capitalize;
}

.button {
    background-color: #21bbea; /* Green */
    opacity: 0.8;
    border: none;
    color: #fff;
    padding: 15px 25px;
    text-align: center;
    text-decoration: none;
    display: block;
    margin: 0 auto;
    font-size: 18px;
    cursor: pointer;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    transition: background-color 0.3s;
    font-family: Arial, sans-serif;
    box-shadow: 0 5px 0 rgba(20, 80, 50, 0.3); /* Dark green shadow */
  }
  
  .button:after {
    content: '';
    background-color: rgba(84, 83, 83, 0.2);
    position: absolute;
    top: 50%;
    left: 50%;
    width: 300%;
    height: 300%;
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    box-shadow: 0 5px 0 rgba(21, 103, 117, 0.8); /* Dark green shadow */
    transition: transform 0.8s, box-shadow 0.3s;
  }
  
  .button:hover {
    transform: translateY(2px); /* Move button down slightly on hover */
    box-shadow: 0 3px 0 rgba(111, 167, 178, 0.8); /* Lighter green shadow */
  }
  
  .button:hover:after {
    transform: translate(-50%, -50%) scale(1);
  }
  
  .button:active {
    background-color: #06606c; /* Darker Green */
  }
  
  


/* CSS for headers */
/* Header.css */

header {
    background-color: #2c3e50; /* Dark blue background */
    padding: 10px 0;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.sky{
   font-size:2rem;
   color: rgb(163, 220, 230);
}
.starter{
    color: #21bbea;
    font-size: 1.5rem;
}
  
.logo a {
    margin-left: 20px;
    text-decoration: none;
    font-family: "Protest Guerrilla", sans-serif;
    font-weight: 400;
    font-style: normal;

}

.nav-links {
    list-style: none;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
}

.nav-links li {
    margin: 0 15px;
}

.nav-links li a {
    color: #fff; /* White color for navigation links */
    text-decoration: none;
    font-size: 1rem;
    transition: color 0.3s ease;
}

.nav-links li a:hover {
    background: linear-gradient(45deg,rgb(163, 220, 230),#21bbea);
    color: transparent; /* Change to orange color on hover */
    background-clip: text;
    font-weight: bolder;
    
}

.menu-toggle {
    display: none; /* Hide by default */
    cursor: pointer;
}

.menu-toggle.open .hamburger {
    transform: rotate(45deg);
}

.menu-toggle.open .hamburger:before,
.menu-toggle.open .hamburger:after {
    top: 0;
    transform: rotate(90deg);
}

.menu-toggle .hamburger {
    width: 25px;
    height: 3px;
    background-color: #fff;
    position: relative;
    transition: transform 0.3s ease;
}

.menu-toggle .hamburger:before,
.menu-toggle .hamburger:after {
    content: '';
    width: 25px;
    height: 3px;
    background-color: #fff;
    position: absolute;
    left: 0;
    transition: transform 0.3s ease;
}

.menu-toggle .hamburger:before {
    top: -8px;
}

.menu-toggle .hamburger:after {
    top: 8px;
}

@media (max-width: 768px) {
    .menu-toggle{
        display: block;
    }
    .nav-links {
        display: none;
        flex-direction: column;
        background-color: #2c3e50;
        position: absolute;
        top: 60px;
        left: 0;
        width: 100%;
    }

    .nav-links.open {
        display: flex;
    }

    .nav-links li {
        width: 100%;
    }
}

