}



/* Footer Section */
.footer {
    background-color: #333;
    color: #fff;
    padding: 50px 0;
    font-family: Arial, sans-serif;
}

.footer-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.footer-links ul,
.social-media ul {
    list-style: none;
    padding: 0;
}

.footer-links ul li,
.social-media ul li {
    display: inline-block;
    margin-right: 20px;
}

.footer-links ul li a,
.social-media ul li a {
    color: #fff;
    text-decoration: none;
    transition: color 0.3s ease;
}

.footer-links ul li a:hover,
.social-media ul li a:hover {
    color: #ffd700; /* Change color on hover */
}

.social-media ul li a i {
    font-size: 24px;
}


.copyright {
    text-align: center;
    margin-top: 20px;
    font-size: 14px;
}

@media (max-width: 768px) {
    .footer-container {
        flex-direction: column;
        text-align: center;
    }

    .footer-links ul,
    .social-media ul {
        margin-bottom: 15px;
    }

    .footer-links ul li,
    .social-media ul li {
        display: block;
        margin-right: 0;
    }
}





