  /* CSS FOR ALL TYPE OF FORM */
.whole-form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: fit-content;
    padding: 20px;
    width: fit-content;
    border-radius: 20px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    background-color: #f0f0f0;

    margin: 30px auto;
}

.animation-container {
    width: 50%;
    flex: 1;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.animation {
    /* Add your animation styles here */
    width: 400px;
    height: 400px;
    background-color: #f582ae;
    /* border-radius: 5% 50% 50% 5% ; */
    background: linear-gradient(45deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0)), 
                linear-gradient(-45deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0));
    background-size: 200% 200%;
    animation: glassAnimation 2s linear infinite;
    border-radius: 50%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2); /* Outer shadow */
}

@keyframes glassAnimation {
    0% {
        background-position: 0 0;
    }
    100% {
        background-position: 200% 200%;
    }
}
.animation img {
   height: 400px;
   width: 400px;
}

.form-container {
    width: 50%;
    flex: 1;
    padding: 20px;
}

.form {
    max-width: 400px;
    margin: 0 auto;
}

.form-title {
    text-align: center;
    margin-bottom: 20px;
}

.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
}

.form-control {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}
p {
    text-align: center;
    margin-top: 20px;
}

@media only screen and (max-width: 768px) {
    .whole-form-container {
        flex-direction: column;
        margin: 20px auto;
        padding: 10px;
    }

    .animation-container,
    .form-container {
        width: 100%;
    }

    .animation {
        width: 100%;
        max-width: 300px;
        height: 300px;
        margin-bottom: 20px;
    }
    .animation img {
        height: 300px;
        width: 300px;
     }

    .form-container {
        padding: 10px;
    }

    .form {
        max-width: 100%;
    }
}


